.header-color {
	background: #FC466B;
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #3F5EFB, #FC466B);
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #3F5EFB, #FC466B);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.background {
	/* top: 0;
	left */
	height:100%;

	background: #FC466B;
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #3F5EFB, #FC466B);
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #3F5EFB, #FC466B);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#about-me, #contact-me h3 {
	text-align: center;
}

.avatar-img {
	object-fit: cover;
	width: 250px;
	height: 250px;
	border-radius: 50%;
	margin-top: 5em;
}

.banner h1, .banner h3, .banner p {
	color: white;
}

.banner {
	background-color: black;
	opacity: .7;
	border-radius: 1em;
	margin-left: 1em;
	margin-right: 1em;
}

.banner h3 {
	padding-top: 0.7em;
	margin: auto;
}

.banner h1 {
	font-size: 56px;
	font-weight: bold;
	margin-bottom: 0;
}

a {
	text-decoration: none;
}

.banner>p {
	font-size: 20px;
	padding: 1em
}

.banner i {
	color: white;
}

.social-links {
	text-align: left;
	margin: auto;
}

#contact-me {
	margin-top: 3.5em;
	/* margin-bottom: 5em; */
}

.social-links table {
	border-spacing: 0 17px;
	padding-left: 1em;
	padding-right: 1em;
}

.social-links i {
	color: white;
	font-size: 4em;
	padding: 8px;
}

.social-links p {
	font-size: 20px;
	color: white;
	margin: auto;
	padding-left: 1em;
	padding-right: 1em;
}